package com.ragnorak.cmparticle.ui.components

import androidx.compose.foundation.Image
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.sp
import com.ragnorak.cmparticle.ui.mainScreen.model.LanguageState
import org.jetbrains.compose.resources.DrawableResource
import org.jetbrains.compose.resources.painterResource

@Composable
fun SetProfile(ivProfile: DrawableResource, darkTheme: Boolean, languageState: LanguageState) {
    Box(contentAlignment = Alignment.Center) {
        Column(horizontalAlignment = Alignment.CenterHorizontally) {
            Image(
                painter = painterResource(ivProfile),
                contentDescription = null,
                modifier = Modifier.weight(1f).clip(CircleShape),
                contentScale = ContentScale.Fit
            )
            Text(
                text = languageState.profile1,
                fontSize = 24.sp,
                fontWeight = FontWeight.ExtraBold,
                color = if (darkTheme) Color.Unspecified else Color.White
            )
            Text(
                text = languageState.profile2,
                fontWeight = FontWeight.ExtraBold,
                color = if (darkTheme) Color.Unspecified else Color.White
            )
            Text(
                text = languageState.profile3,
                fontWeight = FontWeight.ExtraBold,
                color = if (darkTheme) Color.Unspecified else Color.White
            )
        }
    }
}