package com.ragnorak.cmparticle.ui.mainScreen.viewmodel

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import com.ragnorak.cmparticle.domain.usecases.GetLinkProviderUseCase
import com.ragnorak.cmparticle.getDeviceType
import com.ragnorak.cmparticle.ui.core.CurrentLanguage
import com.ragnorak.cmparticle.ui.core.Language
import com.ragnorak.cmparticle.ui.mainScreen.model.LanguageState
import com.ragnorak.cmparticle.ui.mainScreen.model.MainScreenState
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

class MainScreenViewModel(
    getLinkProviderUseCase: GetLinkProviderUseCase,
    private val currentL: CurrentLanguage
) : ViewModel() {

    private val device = getDeviceType()
    private val _uiState = MutableStateFlow(MainScreenState())
    val uiState = _uiState.asStateFlow()
    private val _themeColor = MutableStateFlow(true)
    val themeColor = _themeColor.asStateFlow()
    private val _languageState = MutableStateFlow(initLanguage())
    val languageState = _languageState.asStateFlow()


    init {
        viewModelScope.launch {
            _uiState.update { state ->
                val items = getLinkProviderUseCase()
                val isDesktop = device
                state.copy(
                    items = items,
                    isDesktop = isDesktop,
                    isLoading = false
                )
            }
        }
    }

     fun initLanguage(): LanguageState {
        val currentLanguage = currentL.language
        if (currentLanguage == null) {
            currentL.language = Language.ESP
            return LanguageState.fromLanguage(Language.ESP, device)
        } else {
            return LanguageState.fromLanguage(currentLanguage, device)
        }

    }

    fun changeThemeColor() {
        _themeColor.value = !_themeColor.value
    }

    fun changeLanguage(language: Language) {
        _languageState.value = LanguageState.fromLanguage(language, device)
        currentL.language = language
    }
}
