package com.ragnorak.cmparticle.ui.projectsScreen.model

import com.ragnorak.cmparticle.data.local.Project
import com.ragnorak.cmparticle.getDeviceType
import com.ragnorak.cmparticle.ui.core.Language
import com.ragnorak.cmparticle.ui.core.Language.ENG
import com.ragnorak.cmparticle.ui.core.Language.ESP
import com.ragnorak.cmparticle.ui.core.Language.PORT

data class ProjectScreenState(
    val items: List<Project> = emptyList(),
    val isLoading: Boolean = true,
    val tittleTopBar: String = "",
    val tittleButton: String = "",
    val currentLanguage: String = ""
) {
    fun getTittleTopBar(language: Language): String {
        return when (language) {
            ESP -> "Mis proyectos"
            ENG -> "My projects"
            PORT -> "Meus projetos"
        }
    }

    fun getTittleButton(language: Language): String {
        return when (language) {
            ESP -> "Ir al proyecto"
            ENG -> "Go to projects"
            PORT -> "Ir para o projeto"
        }
    }

    fun getCurrentLanguage(language: Language): String {
        val device = getDeviceType()
        return when (language) {
            ESP -> if (device == "Desktop") "Espaol" else "ESP"
            ENG -> if (device == "Desktop") "English" else "ENG"
            PORT -> if (device == "Desktop") "Portugus" else "POR"
        }
    }

}
