package com.ragnorak.cmparticle.domain.usecases

import com.ragnorak.cmparticle.data.local.Project
import com.ragnorak.cmparticle.domain.Repository
import com.ragnorak.cmparticle.ui.core.Language

class GetProjectsUseCase (private val repository: Repository) {

    operator fun invoke(language: Language): List<Project>{
        return repository.getProjects(language)
    }
}