data class ProjectScreen(val device: String) : Screen {
    @OptIn(KoinExperimentalAPI::class)
    @Composable
    override fun Content() {
        val viewModel = koinViewModel<ProjectScreenViewModel>()
        val state by viewModel.uiState.collectAsState()
        val themeColor by viewModel.themeColor.collectAsState()
        val isDesktop by remember { mutableStateOf(device == "Desktop") }
        val navigator = LocalNavigator.currentOrThrow
        val uriHandler = LocalUriHandler.current
        val colorMain = if (themeColor) Color.Black else Color.LightGray

        LaunchedEffect(key1 = true) {
            viewModel.fetchProjects()
        }

        val infiniteTransition = rememberInfiniteTransition()
        val animatedColor by infiniteTransition.animateColor(
            initialValue = colorMain,
            targetValue = Color.Red,
            animationSpec = infiniteRepeatable(
                animation = tween(1000),
                repeatMode = RepeatMode.Reverse
            )
        )

        Scaffold(topBar = {
            TopBarProjects(
                viewModel,
                state,
                themeColor,
                animatedColor,
                navigator
            )
        }) { paddingValues ->
            Box(
                modifier = Modifier.fillMaxSize().padding(paddingValues),
                contentAlignment = Alignment.Center
            ) {
                if (state.isLoading) {
                    CircularProgressIndicator()
                } else {
                    ContentViewProject(state, animatedColor, isDesktop, uriHandler, themeColor)
                }
            }

        }

    }

    @Composable
    fun ContentViewProject(
        state: ProjectScreenState,
        animatedColor: Color,
        isDesktop: Boolean,
        uriHandler: UriHandler,
        themeColor: Boolean
    ) {
        val backgroundColor = getBackgroundColor(themeColor)
        Column(
            modifier = Modifier.fillMaxSize()
                .background(backgroundColor),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {

            HorizontalDivider(
                modifier = Modifier
                    .fillMaxWidth()
                    .height(12.dp),
                color = animatedColor
            )

            LazyColumn(
                horizontalAlignment = Alignment.CenterHorizontally,
                verticalArrangement = Arrangement.Center,
                userScrollEnabled = true
            ) {
                val projects = state.items
                items(projects) { project ->
                    val showDialog = remember { mutableStateOf(false) }
                    var currentPhotoIndex by remember { mutableStateOf(0) }
                    if (showDialog.value) {
                        ShowDialogImage(
                            project.photo[currentPhotoIndex].resource,
                            project.photo[currentPhotoIndex].type,
                            isDesktop,
                            onDismiss = { showDialog.value = false })
                    }
                    Column(
                        modifier = Modifier.fillMaxWidth(),
                        horizontalAlignment = Alignment.CenterHorizontally
                    ) {
                        // Nombre del proyecto
                        Text(
                            modifier = Modifier.padding(top = 8.dp, bottom = 2.dp),
                            text = project.name,
                            fontStyle = FontStyle.Italic,
                            fontSize = 30.sp,
                            fontWeight = FontWeight.ExtraBold,
                            color = if (themeColor) Color.Unspecified else Color.White
                        )
                        Spacer(modifier = Modifier.size(4.dp))

                        // Contenedor de la imagen con flechas
                        Box(
                            contentAlignment = Alignment.Center,
                            modifier = Modifier.fillMaxWidth()
                        ) {
                            // Flecha izquierda
                            IconButton(
                                onClick = {
                                    if (currentPhotoIndex > 0) currentPhotoIndex-- else currentPhotoIndex =
                                        project.photo.size - 1
                                },
                                modifier = Modifier.align(Alignment.CenterStart)
                                    .padding(start = 1.dp)
                            ) {
                                Icon(
                                    imageVector = Icons.AutoMirrored.Filled.ArrowBack,
                                    contentDescription = "Previous photo",
                                    tint = if (themeColor) Color.Black else Color.White
                                )
                            }

                            Box(
                                modifier = Modifier.fillMaxWidth().height(450.dp)
                                    .align(Alignment.Center), contentAlignment = Alignment.Center
                            ) {
                                if (isDesktop) {
                                    ShowImageDesktop(
                                        project,
                                        currentPhotoIndex
                                    ) { showDialog.value = true }
                                } else {
                                    ShowImageMobile(project, currentPhotoIndex) {
                                        showDialog.value = true
                                    }
                                }

                            }


                            // Flecha derecha
                            IconButton(
                                onClick = {
                                    if (currentPhotoIndex < project.photo.size - 1) currentPhotoIndex++ else currentPhotoIndex =
                                        0
                                },
                                modifier = Modifier.align(Alignment.CenterEnd).padding(end = 1.dp)
                            ) {
                                Icon(
                                    imageVector = Icons.AutoMirrored.Filled.ArrowForward,
                                    contentDescription = "Next photo",
                                    tint = if (themeColor) Color.Black else Color.White
                                )
                            }
                        }
                        Spacer(modifier = Modifier.size(4.dp))

                        // Descripcin del proyecto
                        Text(
                            modifier = Modifier.padding(vertical = 4.dp).padding(horizontal = 8.dp),
                            text = project.description,
                            fontSize = if (isDesktop) 28.sp else 20.sp,
                            fontWeight = FontWeight.Bold,
                            color = if (themeColor) Color.Unspecified else Color.White
                        )
                        Spacer(modifier = Modifier.size(4.dp))

                        // Botn para ir al proyecto
                        Button(
                            modifier = Modifier.height(50.dp).width(200.dp)
                                .padding(top = 6.dp, bottom = 8.dp),
                            onClick = { uriHandler.openUri(project.url) },
                            colors = ButtonDefaults.buttonColors(
                                contentColor = Color.Black,
                                containerColor = Color.DarkGray
                            ),
                            elevation = ButtonDefaults.buttonElevation(24.dp),
                            border = BorderStroke(1.dp, Color.White)
                        ) {
                            Row(verticalAlignment = Alignment.CenterVertically) {
                                Icon(
                                    painter = painterResource(Res.drawable.ic_git),
                                    contentDescription = null,
                                    modifier = Modifier.size(24.dp),
                                    tint = Color.White
                                )
                                Spacer(Modifier.width(8.dp))
                                Text(
                                    text = state.tittleButton,
                                    fontStyle = FontStyle.Italic,
                                    fontSize = 16.sp,
                                    fontWeight = FontWeight.ExtraBold,
                                    color = Color.White
                                )
                            }
                        }

                        // Divider
                        HorizontalDivider(
                            modifier = Modifier
                                .fillMaxWidth()
                                .size(16.dp),
                            color = animatedColor
                        )
                    }
                }
            }
        }
    }

    @OptIn(ExperimentalMaterial3Api::class)
    @Composable
    fun TopBarProjects(
        viewmodel: ProjectScreenViewModel,
        state: ProjectScreenState,
        themeColor: Boolean,
        animatedColor: Color,
        navigator: Navigator
    ) {
        val (expanded, setExpanded) = remember { mutableStateOf(false) }
        TopAppBar(title = {
            Row(
                modifier = Modifier.fillMaxWidth(),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.SpaceBetween
            ) {
                Icon(
                    Icons.AutoMirrored.Filled.ArrowBack,
                    null,
                    tint = Color.White,
                    modifier = Modifier.padding(start = 8.dp)
                        .clickable { navigator.popUntil { it is MainScreen } }
                )
               Text(
                    text = state.tittleTopBar,
                    modifier = Modifier.weight(1f),
                    textAlign = TextAlign.Center,
                    fontSize = 24.sp,
                    fontWeight = FontWeight.ExtraBold,
                    color = animatedColor,
                    fontStyle = FontStyle.Italic,
                )
            }

        }, colors = TopAppBarDefaults.topAppBarColors(Color.DarkGray),
            actions = {
                androidx.compose.material.IconButton(onClick = { setExpanded(true) }) {
                    Row(modifier = Modifier.padding(end = 4.dp)) {
                        androidx.compose.material.Text(
                            state.currentLanguage,
                            fontWeight = FontWeight.Bold
                        )
                        androidx.compose.material.Icon(
                            imageVector = Icons.Filled.ArrowDropDown,
                            contentDescription = null,
                            tint = Color.White
                        )
                    }

                }

                IconButton(onClick = { viewmodel.changeThemeColor() }) {
                    Icon(
                        painterResource(if (themeColor) Res.drawable.ic_moon else Res.drawable.ic_sun),
                        null,
                        tint = if (themeColor) Color.Black else Color.White,
                        modifier = Modifier.size(40.dp).padding(end = 4.dp)
                    )
                }

                DropdownMenu(
                    modifier = Modifier.background(Color.LightGray).padding(horizontal = 16.dp),
                    expanded = expanded,
                    onDismissRequest = { setExpanded(false) }
                ) {
                    SetDropDownItem("Espaol", Res.drawable.ic_spain) {
                        viewmodel.changeLanguage(Language.ESP)
                        setExpanded(false)
                    }
                    SetDropDownItem("English", Res.drawable.ic_england) {
                        viewmodel.changeLanguage(Language.ENG)
                        setExpanded(false)
                    }
                    SetDropDownItem("Portugus", Res.drawable.ic_portugal) {
                        viewmodel.changeLanguage(Language.PORT)
                        setExpanded(false)
                    }
                }
            }
        )
    }

    private fun getBackgroundColor(changeTextColor: Boolean) =
        if (changeTextColor) Brush.horizontalGradient(
            listOf(
                Color.LightGray,
                Color.DarkGray
            )
        ) else Brush.horizontalGradient(listOf(Color.DarkGray, Color.Black))

    @Composable
    private fun ShowImageDesktop(
        project: Project,
        currentPhotoIndex: Int,
        onItemSelected: () -> Unit
    ) {
        when (project.photo[currentPhotoIndex].type) {
            Type.HOR -> {
                Image(
                    painter = painterResource(project.photo[currentPhotoIndex].resource),
                    contentDescription = null,
                    modifier = Modifier
                        .height(900.dp)
                        .width(800.dp)
                        .padding(top = 2.dp).clickable(
                            interactionSource = remember { MutableInteractionSource() },
                            indication = null // Desactiva la sombra y ripple visual
                        ) { onItemSelected() },
                    contentScale = ContentScale.Fit
                )
            }

            Type.VERT -> {
                Image(
                    painter = painterResource(project.photo[currentPhotoIndex].resource),
                    contentDescription = null,
                    modifier = Modifier
                        .height(450.dp)
                        .width(200.dp)
                        .padding(top = 2.dp).clickable(
                            interactionSource = remember { MutableInteractionSource() },
                            indication = null // Desactiva la sombra y ripple visual
                        ) { onItemSelected() },
                    contentScale = ContentScale.Crop
                )
            }

        }


    }

    @Composable
    private fun ShowImageMobile(
        project: Project,
        currentPhotoIndex: Int, onItemSelected: () -> Unit
    ) {
        when (project.photo[currentPhotoIndex].type) {
            Type.HOR -> {
                Image(
                    painter = painterResource(project.photo[currentPhotoIndex].resource),
                    contentDescription = null,
                    modifier = Modifier
                        .height(450.dp)
                        .width(300.dp)
                        .padding(top = 2.dp).clickable { onItemSelected() },
                    contentScale = ContentScale.Fit
                )
            }

            Type.VERT -> {
                Image(
                    painter = painterResource(project.photo[currentPhotoIndex].resource),
                    contentDescription = null,
                    modifier = Modifier
                        .height(450.dp)
                        .width(200.dp)
                        .padding(top = 2.dp).clickable { onItemSelected() },
                    contentScale = ContentScale.Crop
                )
            }

        }
    }
}
