package com.ragnorak.cmparticle.data

import com.ragnorak.cmparticle.data.local.LinkItem
import com.ragnorak.cmparticle.data.local.Project
import com.ragnorak.cmparticle.data.service.LinkProvider
import com.ragnorak.cmparticle.data.service.ProjectsProvider
import com.ragnorak.cmparticle.domain.Repository
import com.ragnorak.cmparticle.ui.core.Language

class RepositoryImpl(
    private val linkProvider: LinkProvider,
    private val projectsProvider: ProjectsProvider
) :
    Repository {
    override fun getLinkProvider(): List<LinkItem> {
        return linkProvider.items
    }

    override fun getProjects(language: Language): List<Project> {
        return projectsProvider.getProjects(language)
    }
}