package com.ragnorak.cmparticle.ui.mainScreen.model

import com.ragnorak.cmparticle.ui.core.Language

data class LanguageState(
    val topBarTittle: String,
    val language: String,
    val profile1: String,
    val profile2: String,
    val profile3: String,
    val projectButton: String,
    val kotlinWebMessage: String,
    val contactMessage: String
) {
    companion object {
        fun fromLanguage(language: Language, device: String): LanguageState {
            return when (language) {
                Language.ESP -> LanguageState(
                    topBarTittle = "Bienvenido/a",
                    language = if (device == "Desktop") "Espaol" else "ESP",
                    profile1 = "Barzabal Cristian Profile",
                    profile2 = "Desarrollador Android y Multiplataforma freelance",
                    profile3 = "Perfil kotlin multiplataforma.",
                    projectButton = "Proyectos",
                    kotlinWebMessage = "Sitio web hecho 100% en Kotlin",
                    contactMessage = "Contactar ahora"
                )
                Language.ENG -> LanguageState(
                    topBarTittle = "Welcome",
                    language = if (device == "Desktop") "English" else "ENG",
                    profile1 = "Barzabal Cristian Profile",
                    profile2 = "Freelance Android and Multiplatform Developer",
                    profile3 = "Cross-platform kotlin profile.",
                    projectButton = "Projects",
                    kotlinWebMessage = "Website made 100% in Kotlin",
                    contactMessage = "Contact now"
                )
                Language.PORT -> LanguageState(
                    topBarTittle = "Bem-vindo",
                    language = if (device == "Desktop") "Portugus" else "POR",
                    profile1 = "Barzabal Cristian Profile",
                    profile2 = "Desenvolvedor Freelancer Android e Multiplataforma",
                    profile3 = "Perfil kotlin multiplataforma.",
                    projectButton = "Projetos",
                    kotlinWebMessage = "Site feito 100% em Kotlin",
                    contactMessage = "Entre em contato agora"
                )
            }
        }
    }
}
