class MainScreen : Screen {
    @Composable
    override fun Content() {
        WebProfile()
    }

}

@OptIn(KoinExperimentalAPI::class)
@Composable
fun WebProfile() {
    val viewModel = koinViewModel<MainScreenViewModel>()
    val state by viewModel.uiState.collectAsState()
    val themeColor by viewModel.themeColor.collectAsState()
    val languageState by viewModel.languageState.collectAsState()
    val isDesktop by remember { mutableStateOf(state.isDesktop == "Desktop") }
    val navigator = LocalNavigator.currentOrThrow
    val uriHandler = LocalUriHandler.current

    Scaffold(topBar = { TopBarHome(viewModel, themeColor, languageState) }) { paddingValues ->
        Box(modifier = Modifier.fillMaxSize().padding(paddingValues), contentAlignment = Alignment.Center) {
            if (state.isLoading) {
                CircularProgressIndicator()
            } else {
                ContentView(state, isDesktop, navigator, uriHandler, themeColor, languageState,viewModel)
            }
        }

    }


}

@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun TopBarHome(viewmodel: MainScreenViewModel, themeColor: Boolean, languageState: LanguageState) {
    val (expanded, setExpanded) = remember { mutableStateOf(false) }
    TopAppBar(title = {
        Row(
            modifier = Modifier.fillMaxWidth(),
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.SpaceBetween
        ) {
            Image(
                painterResource(Res.drawable.iv_kotliniv),
                null,
                modifier = Modifier.size(80.dp).padding(start = 8.dp)
            )
            Text(
                languageState.topBarTittle,
                modifier = Modifier.weight(1f),
                textAlign = TextAlign.Center,
                fontSize = 24.sp,
                fontWeight = FontWeight.ExtraBold,
                color = Color.White,
                fontStyle = FontStyle.Italic,
            )
        }
    }, colors = TopAppBarDefaults.topAppBarColors(Color.DarkGray),
        actions = {
            IconButton(onClick = { setExpanded(true) }) {
                Row(modifier = Modifier.padding(end = 4.dp)) {
                    Text(languageState.language, fontWeight = FontWeight.Bold)
                    Icon(
                        imageVector = Icons.Filled.ArrowDropDown,
                        contentDescription = null,
                        tint = Color.White
                    )
                }

            }

            IconButton(onClick = { viewmodel.changeThemeColor() }) {
                Icon(
                    painterResource(if (themeColor) Res.drawable.ic_moon else Res.drawable.ic_sun),
                    null,
                    tint = if (themeColor) Color.Black else Color.White,
                    modifier = Modifier.size(40.dp).padding(end = 4.dp)
                )
            }

            DropdownMenu(
                modifier = Modifier.background(Color.LightGray).padding(horizontal = 16.dp),
                expanded = expanded,
                onDismissRequest = { setExpanded(false) }
            ) {
                SetDropDownItem("Espaol", Res.drawable.ic_spain) {
                    viewmodel.changeLanguage(Language.ESP)
                    setExpanded(false)
                }
                SetDropDownItem("English", Res.drawable.ic_england) {
                    viewmodel.changeLanguage(Language.ENG)
                    setExpanded(false)
                }
                SetDropDownItem("Portugus", Res.drawable.ic_portugal) {
                    viewmodel.changeLanguage(Language.PORT)
                    setExpanded(false)
                }
            }
        }
    )
}

@Composable
fun ContentView(
    state: MainScreenState,
    isDesktop: Boolean,
    navigator: Navigator,
    uriHandler: UriHandler,
    themeColor: Boolean,
    languageState: LanguageState,
    viewModel: MainScreenViewModel
) {

    val verticalScroll = rememberScrollState(initial = 0)

    LaunchedEffect(key1 = true){
        viewModel.initLanguage()
    }

    Column(
        modifier = Modifier.fillMaxSize()
            .background(
                if (themeColor) Brush.horizontalGradient(
                    listOf(
                        Color.LightGray,
                        Color.DarkGray
                    )
                ) else Brush.horizontalGradient(listOf(Color.DarkGray, Color.Black))
            ).verticalScroll(state = verticalScroll),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Row(
            modifier = Modifier.fillMaxWidth().weight(1f).padding(top = 8.dp),
            horizontalArrangement = Arrangement.SpaceAround,
            verticalAlignment = Alignment.CenterVertically
        ) {
            if (isDesktop) {
                SetIcon(
                    Res.drawable.ic_apple,
                    "Apple",
                    Color.DarkGray,
                    Color.LightGray,
                    themeColor
                )
                SetIcon(
                    Res.drawable.ic_androidb,
                    "Android",
                    Color.DarkGray,
                    Color.LightGray,
                    themeColor
                )
                SetProfile(
                    Res.drawable.icprofile2,
                    themeColor,
                    languageState
                )
                SetIcon(
                    Res.drawable.ic_web,
                    "Web",
                    Color.LightGray,
                    Color.DarkGray,
                    themeColor
                )
                SetIcon(
                    Res.drawable.ic_desktop,
                    "Desktop",
                    Color.LightGray,
                    Color.DarkGray,
                    themeColor
                )
            } else {
                SetProfile(
                    Res.drawable.icprofile2,
                    themeColor,
                    languageState
                )
            }
        }

        for (item in state.items) {
            GenerateButton(item.url, uriHandler, item, navigator, state.isDesktop, languageState)
        }

        Column(
            modifier = Modifier.weight(1f),
            verticalArrangement = Arrangement.SpaceAround,
            horizontalAlignment = Alignment.CenterHorizontally
        ) {

            var isRed by remember { mutableStateOf(false) }
            val colorMain = if (themeColor) Color.Black else Color.White
            val animatedColor by animateColorAsState(
                targetValue = if (isRed) {
                    Colors.kotlin
                } else {
                    colorMain
                },
                animationSpec = androidx.compose.animation.core.tween(durationMillis = 1000)
            )

            LaunchedEffect(Unit) {
                while (true) {
                    delay(1000)
                    isRed = !isRed
                }
            }

            Text(
                text = languageState.kotlinWebMessage, fontSize = 24.sp,
                fontWeight = FontWeight.Bold, color = animatedColor
            )
            Spacer(modifier = Modifier.size(4.dp))
            ContactButton(themeColor, languageState)
        }
    }

}

@Composable
fun ContactButton(darkTheme: Boolean, languageState: LanguageState) {
    var showContent by remember { mutableStateOf(false) }
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.SpaceAround
    ) {
        Button(
            modifier = Modifier.height(60.dp).width(300.dp),
            onClick = { showContent = !showContent },
            colors = ButtonDefaults.buttonColors(
                contentColor = Color.Black,
                backgroundColor = Color.DarkGray
            ),
            elevation = ButtonDefaults.elevation(24.dp),
            border = BorderStroke(1.dp, Color.White)

        ) {
            Text(
                text = languageState.contactMessage,
                fontSize = if (languageState.topBarTittle == "Bem-vindo") 20.sp else 24.sp,
                fontWeight = FontWeight.ExtraBold,
                color = Color.White
            )
        }
        AnimatedVisibility(showContent) {
            Column(
                horizontalAlignment = Alignment.CenterHorizontally,
                verticalArrangement = Arrangement.Center
            ) {
                Spacer(modifier = Modifier.height(16.dp))
                Row(verticalAlignment = Alignment.CenterVertically) {

                    Icon(
                        modifier = Modifier.size(24.dp),
                        painter = painterResource(Res.drawable.ic_what),
                        contentDescription = null, tint = Color.Green
                    )
                    Spacer(modifier = Modifier.width(8.dp))
                    Text(
                        text = "+5491168298219",
                        fontWeight = FontWeight.ExtraBold,
                        color = if (darkTheme) Color.Unspecified else Color.White
                    )
                }
                Spacer(modifier = Modifier.height(8.dp))
                Row(verticalAlignment = Alignment.CenterVertically) {

                    Image(
                        modifier = Modifier.size(34.dp),
                        painter = painterResource(Res.drawable.ic_gmail),
                        contentDescription = null
                    )
                    Spacer(modifier = Modifier.width(8.dp))
                    Text(
                        text = "crbarzabal@gmail.com",
                        fontWeight = FontWeight.ExtraBold,
                        color = if (darkTheme) Color.Unspecified else Color.White
                    )
                }
            }
        }
    }
}