package com.ragnorak.cmparticle.ui.projectsScreen.viewmodel

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import com.ragnorak.cmparticle.domain.usecases.GetProjectsUseCase
import com.ragnorak.cmparticle.ui.core.CurrentLanguage
import com.ragnorak.cmparticle.ui.core.Language
import com.ragnorak.cmparticle.ui.projectsScreen.model.ProjectScreenState
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

class ProjectScreenViewModel(
    private val getProjectsUseCase: GetProjectsUseCase,
    private val currentL: CurrentLanguage
) : ViewModel() {

    private val _uiState = MutableStateFlow(ProjectScreenState())
    val uiState = _uiState.asStateFlow()
    private val _themeColor = MutableStateFlow(true)
    val themeColor = _themeColor.asStateFlow()

    init {
        fetchProjects()
    }


    fun fetchProjects() {
        val language = currentL.language!!
        viewModelScope.launch {
            _uiState.update {
                val projects = getProjectsUseCase(language)
                it.copy(
                    items = projects,
                    isLoading = false,
                    tittleTopBar = it.getTittleTopBar(language),
                    tittleButton = it.getTittleButton(language),
                    currentLanguage = it.getCurrentLanguage(language)
                )
            }
        }
    }

    fun changeThemeColor() {
        _themeColor.value = !_themeColor.value
    }

    fun changeLanguage(language: Language) {
        currentL.language = language
        fetchProjects()

    }
}